import interfaceurl from '@/utils/interface.js'
import authPage from '@/components/authorization-page.vue' //引入授权窗体
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	components: {
		authPage
	},
	computed: mapState(['userInfos']),
	data() {
		return {
			userInfo: {}, //获取用户信息
		}
	},
	watch:{
		userInfos(val) {
			//用户的授权信息存本地，全局变量是为了第一次授权时快速响应
			if(val) {
				this.userInfo = uni.getStorageSync('userInfo')
			}
		}
	},
	onLoad() {

	},
	onShow() {
		let token = uni.getStorageSync('token')
		this.userInfo = this.userInfos.nickName ? this.userInfos : uni.getStorageSync('userInfo')
	},
	methods: {
		prevent() {},
		turnOrder() {
			uni.navigateTo({
			    url: '/pages/my/order'
			});
		},
		turnPage() {
			uni.navigateTo({
			    url: '/pages/my/reservation'
			});
		},
		turnLogin() {
			uni.navigateTo({
			    url: '/pages/index/login'
			});
		}
	}
}
